.footer {
    background-color: #7E4628;
    color: #ffffff70;
    font-weight: 400;
}

.footer__container {
    margin: 0 auto;
    max-width: 1095px;
    padding: 14px 15px;
    display: flex;
    column-gap: 25px;
    justify-content: space-between;
    align-items: center;
}

.footer__contacts {
    display: flex;
    flex-direction: column;
}

@media (max-width: 425px) {
    .footer__container {
        font-size: 12px;
    }
}