.modal {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: end;
    align-items: stretch;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease 0s;
}

.modal.active {
    opacity: 1;
    visibility: visible;
}

.modal__box {
    display: flex;
    flex-direction: column;
    background-color: #F9F9F9;
    border-radius: 20px;
    color: #242424;
    transform: translateX(100%);
    transition: transform 0.3s ease 0s;
    position: relative;
    top: 0;
    left: 0;
    touch-action: none;
}

.modal__box.resetting {
    transition: top 0.3s ease;
    top: 0px !important;
}

.modal.active .modal__box {
    transform: translateX(0);
}

.modal__close {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal__close::before {
    content: "+";
    font-size: 40px;
    line-height: 0.5;
    transform: rotate(45deg);
    color: #888888;
}

.modal__panel {
    padding: 24px 24px 0px 24px;
}

.panel {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.panel__icon {
    width: 44px;
    height: 44px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.panel__icon img {}

.panel__title {
    font-weight: 600;
    font-size: 16px;
    line-height: 1.2;
}

.modal__content {
    overflow-y: auto;
    overflow-x: hidden;
    flex: 1 1 auto;
    padding: 24px;
}

.modal__productList {
    margin-bottom: 16px;
}

.productList {
    display: flex;
    flex-direction: column;
    row-gap: 15px;
}

.emptyFavourites {
    text-align: left;
}

.emptyFavourites__title {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 10px;
}

.emptyFavourites__text {
    font-size: 16px;
    color: #A2A2A2;
    margin-bottom: 10px;
}

@media (max-width: 425px) {
    .modal {
        padding-top: 80px;
    }

    .modal__box {
        width: 100%;
        transform: translate(0, 100%);
    }

    .modal.active .modal__box {
        transform: translate(0, 0);
    }

    .modal__content {
        padding: 18px;
    }
}