.typeList {
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 16px;
    font-weight: 500;
    line-height: 1.5;
    margin-bottom: 31px;
}

.typeList__item {
    cursor: pointer;
    padding: 4px 8px;
    background-color: #ededed90;
    border-radius: 6px;
    transition: all 0.2s ease 0s;
    user-select: none;
}

.typeList__item button {
    background-color: transparent;
}

.typeList__item:hover {
    background-color: #c67c4e81;
}

.typeList__item.active {
    font-weight: 600;
    color: #fff;
    background-color: #C67C4E;
}

@media (max-width: 1095px) {
    .typeList {
        margin-bottom: 15px;
    }
}

@media (max-width: 425px) {
    .typeList {
        column-gap: 8px;
    }
}