.auth_main {
    background: url(../assets/images/Background.jpg) 0 0/100% auto no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
}

.authForm {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 35px 60px;
    background-color: #ffffff15;
    border-radius: 30px;
    color: #E8E7E7;
    width: 475px;
}

.authForm button {
    margin-bottom: 14px;
}

.authForm button:disabled {
    background-color: #ad6e46;
}

.authForm__title {
    font-weight: 600;
    font-size: 24px;
    margin-bottom: 22px;
}

.authForm__text {
    font-size: 16px;
    font-weight: 600;
}

.authForm__text a {
    color: #C67C4E;
}

.authForm__error {
    align-self: start;
    color: rgba(201, 17, 17, 0.603);
    padding-left: 7px;
    margin-bottom: 5px;
    margin-top: -15px;
}