.searchBar {
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    column-gap: 16px;
    order: 2;
}

@media (max-width: 739px) {
    .searchBar {
        order: 3;
    }
}

.searchBar__input {
    flex: 1 1 auto;
    background-color: #2A2A2A;
    padding: 16px;
    display: flex;
    align-items: center;
    column-gap: 8px;
    color: #fff;
    border-radius: 12px;
}

.searchIcon {
    width: 20px;
    height: 20px;
}

.searchIcon img {
    width: 100%;
    height: 100%;
}

.searchBar__input input {
    background-color: transparent;
    width: 100%;
}

.searchBar__input input::placeholder {
    color: #A2A2A2;
}

.searchBar__filterButton {
    cursor: pointer;
    width: 52px;
    height: 52px;
    border-radius: 12px;
    background-color: #C67C4E;
    display: flex;
    justify-content: center;
    align-items: center;
}