.productCard {
    flex: 0 1 calc((100% - 60px) / 4);
    padding: 8px;
    display: flex;
    flex-direction: column;
    background-color: #fff;
    border-radius: 16px;
    margin: 7.5px;
}

.productCard__image {
    width: 100%;
    padding-top: 100%;
    align-self: center;
    margin-bottom: 10px;
    cursor: pointer;
    border-radius: 12px;
    user-select: none;
    overflow: hidden;
    position: relative;
}

.productCard__image img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.productCard__info {
    padding: 0px 22px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.productCard__text {
    font-weight: 600;
    line-height: 1.5;
}

.productCard__name {
    font-size: 16px;
    margin-bottom: 4px;
}

.productCard__type {
    color: #A2A2A2;
    font-size: 12px;
    line-height: 1.2;
    font-weight: 400;
    margin-bottom: 9px;
}

.productCard__price {
    font-size: 18px;
}

.productCard__button {
    flex: 0 0 42px;
    width: 42px;
    height: 42px;
    background-color: #C67C4E;
    border-radius: 8px;
    position: relative;
    transition: background-color 0.2s ease 0s;
}

.productCard__button:hover {
    background-color: #9e613b;
}

.productCard__button:active {
    transform: scale(0.92);
}

.productCard__button::before {
    content: "";
    display: block;
    width: 15px;
    height: 2px;
    background-color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.productCard__button::after {
    content: "";
    display: block;
    width: 2px;
    height: 15px;
    background-color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@media (max-width: 1095px) {
    .productCard {
        flex: 0 1 48%;
    }
}

@media (max-width: 782px) {
    .productCard {
        flex: 0 1 45%;
    }
}

@media (max-width: 570px) {
    .productCard {
        flex: 0 1 80%;
    }
}