.customInput_wrapper {
    align-self: stretch;
    padding: 16px;
    background-color: #E8E7E7;
    border-radius: 12px;
    margin-bottom: 27px;
}

.customInput {
    width: 100%;
    font-size: 14px;
    color: #4d4d4d;
}

.customInput::placeholder {
    color: #A2A2A2;
}

input:-webkit-autofill,
input:-webkit-autofill:focus {
    transition: background-color 0s 600000s, color 0s 600000s !important;
}