.counter {
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 18px;
}

.counter__button {
    background-color: #fff;
    height: 28px;
    width: 28px;
    border-radius: 50%;
    border: 1px solid #F9F2ED;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 22px;
}

.counter__button:first-child {
    padding-bottom: 2px;
}

.counter__number {
    font-weight: 600;
    font-size: 16px;
}

@media (max-width: 425px) {
    .counter {
        column-gap: 12px;
    }

    .counter__number {
        font-weight: 600;
        font-size: 14px;
    }
}