.banner {
    width: 691px;
    height: 296px;
    padding: 40px;
    margin: 0px auto 31px auto;
    color: #fff;
    font-weight: 600;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: start;
}

.banner img {
    border-radius: 16px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: -1;
}

.banner__text {
    display: inline-block;
    padding: 5.5px 13px;
    background-color: #ED5151;
    border-radius: 8px;
    line-height: 19px;
    margin-bottom: 8px;
    font-size: 15px;
}

.banner__title {
    display: inline-block;
    width: 200px;
    line-height: 40px;
    font-size: 32px;
    text-shadow: 5px 4px 3px #4D2710;
}

@media (max-width: 739px) {
    .banner {
        width: 90%;
        height: 240px;
        padding: 30px;
    }
}

@media (max-width: 600px) {
    .banner {
        height: 200px;
    }
}

@media (max-width: 500px) {
    .banner {
        height: 180px;
    }
}

@media (max-width: 500px) {
    .banner {
        height: 180px;
    }
}

@media (max-width: 425px) {
    .banner {
        padding: 15px;
        height: 130px;
    }

    .banner__text {
        padding: 5px 11px;
        margin-bottom: 4px;
        font-size: 14px;
    }

    .banner__title {
        width: 170px;
        font-size: 26px;
        line-height: 33px;
    }
}

@media (max-width: 400px) {
    .banner {
        height: 125px;
    }
}

@media (max-width: 330px) {
    .banner {
        height: 100px;
    }
}