.coffee__panel {}

.panel {
    font-size: 16px;
    font-weight: 600;
    line-height: 1.2;
    color: #242424;
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 95px;
    margin-bottom: 20px;
}

.panel__back {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.panel__title {}

.panel__favourite {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.popup-message {
    position: absolute;
    top: -25px;
    /* left: 30px; */
    background-color: #fff;
    box-shadow: 1px 1px 5px 0px #000;
    padding: 5px 10px;
    border-radius: 5px;
    font-size: 14px;
    white-space: nowrap;
    z-index: 1;
}

.coffee__card {}

.card {
    display: flex;
    justify-content: center;
    align-items: stretch;
    column-gap: 40px;
    margin-bottom: 21px;
    padding: 15px;
}

.card__image {
    width: 300px;
    height: 300px;
    object-fit: cover;
    border-radius: 16px;
}

.card__details {}

.details {}

.details__box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 16px;
}

.details__text {
    font-weight: 600;
    color: #242424;
}

.details__name {
    font-size: 20px;
    margin-bottom: 4px;
}

.details__prop {
    font-size: 12px;
    line-height: 1.2;
    font-weight: 500;
    color: #A2A2A2;
    margin-bottom: 16px;
}

.details__rating {
    display: flex;
    align-items: end;
}

.details__rating-icon {
    margin-right: 4px;
}

.details__rating-value {
    margin-right: 4px;
    font-size: 16px;
    line-height: 1.2;
}

.details__rating-count {
    font-size: 12px;
    font-weight: 500;
    color: #A2A2A2;
}

.details__icons {
    display: flex;
    column-gap: 12px;
}

.details__icon-box {
    width: 44px;
    height: 44px;
    background-color: #EDEDED;
    border-radius: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.details__icon {
    width: 20px;
    height: 20px;
}

.details__line {
    margin: 0 auto;
    width: 85%;
    height: 1px;
    background-color: #E3E3E3;
    margin-bottom: 16px;
}

.details__size {}

.size {}

.size__title {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 16px;
}

.size__buttons {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
}

.size__button {
    width: 96px;
    height: 41px;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #E3E3E3;
    border-radius: 12px;
}

.size__button.active {
    background-color: #F9F2ED;
    border: 1px solid #C67C4E;
    color: #C67C4E;
}

.details__purchase {
    padding: 16px 0px;
}

.purchase {
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 50px;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
}

.purchase__price {}

.purchase__price-label {
    font-weight: 500;
    line-height: 1.2;
    color: #909090;
    margin-bottom: 4px;
}

.purchase__price-value {
    font-size: 18px;
    font-weight: 600;
    color: #C67C4E;
}

.purchase__button {}

.coffee__description-title {
    max-width: 710px;
    margin: 0 auto;
    font-size: 16px;
    font-weight: 600;
    color: #242424;
    margin-bottom: 8px;
    padding: 0px 10px;
}

.coffee__description-text {
    text-align: justify;
    max-width: 710px;
    margin: 0 auto;
    font-weight: 400;
    color: #A2A2A2;
    padding: 0px 10px;
}

@media (max-width: 768px) {
    .card {
        flex-direction: column;
        align-items: center;
        row-gap: 20px;
    }
}

@media (max-width: 425px) {
    .purchase {
        display: flex;
        justify-content: space-between;
        align-items: center;
        column-gap: 30px;
        color: #fff;
        font-size: 16px;
        font-weight: 600;
    }

    .card__image {
        height: 260px;
    }

    .size__button {
        width: 85px;
    }

    .purchase button {
        min-width: 170px;
    }
}