.modal {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: end;
    align-items: stretch;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease 0s;
}

.modal.active {
    opacity: 1;
    visibility: visible;
}

.modal__box {
    display: flex;
    flex-direction: column;
    background-color: #F9F9F9;
    border-radius: 20px;
    color: #242424;
    transform: translate(100%);
    transition: transform 0.3s ease 0s;
    position: relative;
    top: 0;
    left: 0;
    touch-action: none;
}

.modal__box.resetting {
    transition: top 0.3s ease;
    top: 0px !important;
}

.modal.active .modal__box {
    transform: translate(0, 0);
}

.modal__close {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal__close::before {
    content: "+";
    font-size: 40px;
    line-height: 0.5;
    transform: rotate(45deg);
    color: #888888;
}

.modal__panel {
    padding: 24px 24px 0px 24px;
}

.panel {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.panel__icon {
    width: 44px;
    height: 44px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.panel__icon img {}

.panel__title {
    font-weight: 600;
    font-size: 16px;
    line-height: 1.2;
}

.modal__content {
    overflow-y: auto;
    overflow-x: hidden;
    flex: 1 1 auto;
    padding: 24px;
}

.modal__options {
    margin-bottom: 24px;
}

.options {
    background-color: #EDEDED;
    border-radius: 12px;
    padding: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.options__button {
    flex: 1 1 auto;
    width: 150px;
    padding: 8px 24px;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.2;
    border-radius: 8px;
}

.options__button.active {
    background-color: #C67C4E;
    font-weight: 600;
    color: #fff;
}

.modal__address {
    margin-bottom: 16px;
}

.address {}

.address__title {
    font-weight: 600;
    font-size: 16px;
    margin-bottom: 16px;
}

.address__city {
    display: block;
    width: 100%;
    font-weight: 600;
    margin-bottom: 4px;
}

.address__street {
    display: block;
    width: 100%;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.2;
    margin-bottom: 16px;
}

.edit {
    border: 1px solid #ccc;
    border-radius: 6px;
    padding: 4px;
    transition: all 0.15s ease;
}

.edit:focus {
    border-bottom: 2px solid #a0522d;
    border-right: 2px solid #a0522d;
    background: linear-gradient(90deg, #f5deb3, #ffe4b5);
    transition: all 0.15s ease;
}

.address__button {
    padding: 6px 12px;
    background-color: #fff;
    border-radius: 16px;
    border: 1px solid #A2A2A2;
}

.line {
    margin: 0 auto;
    width: 92%;
    height: 2px;
    background-color: #E3E3E3;
    margin-bottom: 8px;
}

.modal__productList {
    margin-bottom: 8px;
}

.productList {
    display: flex;
    flex-direction: column;
}

.emptyBasket {
    font-size: 20px;
    font-weight: 600;
    text-align: center;
    margin-bottom: 20px;
}

.separator {
    width: 118%;
    height: 4px;
    background-color: #F9F2ED;
    margin-bottom: 16px;
    transform: translateX(-24px)
}

.modal__payment {}

.payment {
    font-size: 14px;
}

.payment__title {
    font-weight: 600;
    font-size: 16px;
    margin-bottom: 16px;
}

.payment__price {
    display: flex;
    justify-content: space-between;
}

.payment__priceText {
    font-weight: 500;
}

.payment__priceNumber {
    font-weight: 600;
}

.payment__delivery {
    display: flex;
    justify-content: space-between;
    margin-bottom: 32px;
}

.payment__deliveryText {
    font-weight: 500;
}

.payment__deliveryNumber {
    font-weight: 600;
}

.modal__payment button {
    font-weight: 600;
    width: 100%;
}

@media (max-width: 425px) {
    .modal {
        padding-top: 80px;
    }

    .modal__box {
        width: 100%;
        transform: translate(0, 100%);
    }

    .modal.active .modal__box {
        transform: translate(0, 0);
    }

    .modal__content {
        padding: 18px;
    }
}