.iconMenu {
    display: flex;
    column-gap: 40px;
    order: 3;
}

.iconMenu__item a {
    display: block;
    height: 35px;
    cursor: pointer;
}

.iconMenu__item img {
    height: 100%;
}

@media (max-width: 739px) {
    .iconMenu {
        order: 2;
    }
}

@media (max-width: 425px) {
    .iconMenu {
        column-gap: 18px;
    }

    .iconMenu__item a {
        height: 31px;
    }
}