.product {
    touch-action: none;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    top: 0;
    left: 0;
    padding: 15px;
    border-radius: 16px;
    transition: background-color 0.3s ease;
}

.product.moving {
    background-color: #cfcfcf;
}

.product.removing {
    animation: fadeOutAndSlide 0.3s ease forwards;
    pointer-events: none;
}

.product.resetting {
    transition: left 0.3s ease, background-color 0.3s ease;
    left: 0 !important;
    background-color: transparent !important;
}

.product.removing .product__delete {
    opacity: 1 !important;
}

@keyframes fadeOutAndSlide {
    0% {
        opacity: 1;
        transform: translateX(0);
    }

    100% {
        opacity: 0;
        transform: translateX(100px);
    }
}

.product__imageBox {
    width: 65px;
    height: 65px;
    border-radius: 12px;
    overflow: hidden;
    margin-right: 16px;
    position: relative;
}

.product__delete {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(255, 0, 0, 0.342);
    opacity: 0;
    transition: all 0.2s ease 0s;
}

.product__delete:hover {
    opacity: 1;
}

.product__delete::before {
    content: "+";
    font-size: 40px;
    line-height: 0.5;
    transform: rotate(45deg);
    color: #690202;
}

.product__imageBox img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.product__textBox {
    flex: 1 1 auto;
    margin-right: 45px;
}

.product__name {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 4px;
}

.product__type {
    font-size: 14px;
    font-weight: 500;
    line-height: 1.2;
}

@media (max-width: 425px) {
    .product__imageBox {
        width: 50px;
        height: 50px;
        margin-right: 14px;
    }

    .product__textBox {
        margin-right: 20px;
    }

    .product__name {
        font-size: 16px;
    }

    .product__type {
        font-size: 12px;
    }
}