.header {
    background-color: #232323;
    height: 120px;
}

.header__container {
    margin: 0 auto;
    max-width: 1095px;
    padding: 0px 15px;
}

.header__navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 40px;
    padding: 35px 0px;
}

.logo {
    color: white;
    font-size: 24px;
    font-weight: 600;
    user-select: none;
    order: 1;
}


@media (max-width: 739px) {
    .header__navigation {
        flex-wrap: wrap;
        column-gap: 15px;
        row-gap: 20px;
        padding: 20px 0px;
    }

    .logo {
        font-size: 22px;
    }
}

@media (max-width: 700px) {
    .header {
        height: 155px;
    }
}