.modal {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease 0s;
}

.modal.active {
    opacity: 1;
    visibility: visible;
}

.modal__box {
    padding: 75px 55px;
    background-color: #F9F9F9;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}

.modal__text {
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 50px;
}

.modal__link {
    cursor: pointer;
    background-color: #C67C4E;
    color: #E8E7E7;
    font-size: 16px;
    padding: 16px 80px;
    border-radius: 16px;
}

.modal__close {
    width: 40px;
    height: 40px;
    position: absolute;
    top: 15px;
    right: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal__close::before {
    content: "+";
    font-size: 40px;
    line-height: 0.5;
    transform: rotate(45deg);
    color: #888888;
}

@media (max-width: 425px) {
    .modal__box {
        padding: 60px 20px;
    }

    .modal__text {
        margin-bottom: 40px;
    }
}