.heart.active {
    animation: tranformation 0.5s forwards;
}

.heart.unActive {
    animation: tranformationBack 0.5s forwards;
}

.heart__filling.active {
    animation: changeColor 0.5s forwards;
}

.heart__filling.unActive {
    animation: changeColorBack 0.5s forwards;
}

@keyframes changeColor {
    0% {
        fill: transparent;
    }

    100% {
        fill: red;
    }
}

@keyframes tranformation {
    0% {
        transform: rotate(0deg) scale(1);
    }

    33% {
        transform: rotate(30deg) scale(0.8);
    }

    66% {
        transform: rotate(-30deg) scale(0.8);
    }

    100% {
        transform: rotate(0deg) scale(1.2);
    }
}

@keyframes changeColorBack {
    0% {
        fill: red;
    }

    100% {
        fill: transparent;
    }
}

@keyframes tranformationBack {
    0% {
        transform: scale(1.2);
    }

    33% {
        transform: scale(0.8);
    }

    66% {
        transform: scale(0.8);
    }

    100% {
        transform: scale(1);
    }
}