.product {}

.product.removing {
    animation: fadeOut 0.3s ease forwards;
    pointer-events: none;
}

@keyframes fadeOut {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

.product__imageBox {
    min-width: 260px;
    padding-top: 100%;
    border-radius: 8px;
    overflow: hidden;
    margin-right: 16px;
    position: relative;
}

.product__imageBox img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.product__info {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
}

.product__textBox {
    flex: 1 1 auto;
}

.product__name {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 4px;
}

.product__type {
    font-size: 12px;
    font-weight: 500;
    line-height: 1.2;
}